// Theme detection and management
function initializeTheme() {
  // Detect Office theme
  if (Office.context && Office.context.officeTheme) {
    applyTheme(Office.context.officeTheme);

    // Listen for theme changes
    Office.context.officeTheme.onThemeChanged = function(args) {
      applyTheme(args.theme);
    };
  } else {
    // Fallback: detect system dark mode preference
    detectSystemTheme();
  }
}

function applyTheme(theme) {
  const body = document.body;

  // Remove existing theme classes
  body.classList.remove('light-theme', 'dark-theme', 'colorful-theme');

  // Apply theme based on Office theme
  if (theme && theme.bodyBackgroundColor) {
    // Convert Office theme colors to determine if dark mode
    const bgColor = theme.bodyBackgroundColor;
    const isDark = isColorDark(bgColor);

    if (isDark) {
      body.classList.add('dark-theme');
      updateThemeColors(theme, true);
    } else {
      body.classList.add('light-theme');
      updateThemeColors(theme, false);
    }
  } else {
    // Fallback to system preference
    detectSystemTheme();
  }
}

function detectSystemTheme() {
  const body = document.body;

  if (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) {
    body.classList.add('dark-theme');
  } else {
    body.classList.add('light-theme');
  }

  // Listen for system theme changes
  if (window.matchMedia) {
    window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', (e) => {
      body.classList.remove('light-theme', 'dark-theme');
      body.classList.add(e.matches ? 'dark-theme' : 'light-theme');
    });
  }
}

function isColorDark(color) {
  // Convert color to RGB and calculate luminance
  let r, g, b;

  if (color.startsWith('#')) {
    // Hex color
    const hex = color.slice(1);
    r = parseInt(hex.substr(0, 2), 16);
    g = parseInt(hex.substr(2, 2), 16);
    b = parseInt(hex.substr(4, 2), 16);
  } else if (color.startsWith('rgb')) {
    // RGB color
    const matches = color.match(/\d+/g);
    r = parseInt(matches[0]);
    g = parseInt(matches[1]);
    b = parseInt(matches[2]);
  } else {
    // Default to light theme if can't parse
    return false;
  }

  // Calculate relative luminance
  const luminance = (0.299 * r + 0.587 * g + 0.114 * b) / 255;
  return luminance < 0.5;
}

function updateThemeColors(theme, isDark) {
  // Apply Office theme colors to CSS custom properties
  const root = document.documentElement;

  if (theme.bodyBackgroundColor) {
    root.style.setProperty('--office-bg-color', theme.bodyBackgroundColor);
  }
  if (theme.bodyForegroundColor) {
    root.style.setProperty('--office-text-color', theme.bodyForegroundColor);
  }
  if (theme.controlBackgroundColor) {
    root.style.setProperty('--office-control-bg', theme.controlBackgroundColor);
  }
  if (theme.controlForegroundColor) {
    root.style.setProperty('--office-control-text', theme.controlForegroundColor);
  }
}

// Function to analyze the contract
async function analyzeContract() {
  updateStatus("Analyzing contract...", "info");
  showProgressSection("Extracting document content...");

  try {
    // Get document content
    const documentText = await Word.run(async (context) => {
      const body = context.document.body;
      body.load("text");
      await context.sync();
      return body.text;
    });

    if (!documentText || documentText.trim().length === 0) {
      throw new Error("No content found in document");
    }

    showProgressSection("Sending to AI for analysis...");

    // Call the API
    const analysisResult = await callAnalysisAPI(documentText);

    // Display results
    displayAnalysisResults(analysisResult);
    updateStatus("Analysis complete", "success");
    hideProgressSection();

  } catch (error) {
    handleError(error);
  }
}

